.item {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.item a {
    text-decoration: none;
}
.item img {
    width: 50px;
    height: 50px;
}
.name {
    color: #E2E8E4;
    font-size: 1.2rem;
    font-style: italic;
}
.name:hover {
    color: #FFCCBB;
}
.name .active {
    color: #FFCCBB;
    font-weight: 900;
}