.nav {
    margin-top: 15px;
    grid-area: n;
    background-color: #006C84;
    border-radius: 5px;
}
.item a {
    color: #E2E8E4;
    text-decoration: none;
    font-size: 20px;
}
.item a:hover {
    color: #FFCCBB;
}
.item {
    padding-bottom: 20px;
    list-style-type: none;
}
.item a.active {
    font-weight: 900;
    color: #FFCCBB;
}
.sidebar {
    margin-top: 300px;
    padding: 10px;
    color: #E2E8E4;
}
.sidebarElements {
    display: flex;
    justify-content: space-between;
    align-items: center;
}