.item {
    padding: 10px;
    margin: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #6EB5C0;
    box-shadow: 0 0 10px #E2E8E4; /* Параметры тени */
}
.item img {
    width: 50px;
    height: 50px;
    border: 30px;
}
.item div {
    float: right;
}