.postsBlock {
    padding: 10px;
}
.postsBlock h3 {
    color: #6EB5C0;
}
.textarea {
    margin-right: 10px;
}
.textarea textarea {
    border: 2px solid #6EB5C0;
    width: 100%;
    color: #6EB5C0;
    outline: none;
    margin-bottom: 10px;
}
.button {
    display: flex;
    justify-content: flex-end;
}
.button button {
    border-radius: 4px;
    background-color: #6EB5C0;
    border: none;
    color: #E2E8E4;
    text-align: center;
    font-size: 1.25rem;
    padding: 10px;
    width: 200px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
  }
  
  span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  button:hover span {
    padding-right: 25px;
    color: #FFCCBB;
  }
  
  button:hover span:after {
    opacity: 1;
    right: 0;
    color: #FFCCBB;
  }
.posts {
    margin-top: 20px;
}