.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialogItems {
    padding: 10px;
}
.dialogItems a {
    color: #6EB5C0;
    font-weight: bold;
    text-decoration-color: #FFCCBB;
}
.dialogItems .active {
    color: #FFCCBB;
}
.dialogItems a:hover {
    color: #FFCCBB;
}
.dialog {
    padding-bottom: 15px;
    color: rgba(46, 139, 87);
}
.messages {
    padding: 10px;
}
.messages .message {
    padding-bottom: 17px;
    color: #6EB5C0;
}
.textarea {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.textarea textarea {
    border: 2px solid #6EB5C0;
    color: #6EB5C0;
    outline: none;
    margin-bottom: 10px;
    width: 100%;
}
.button button {
    border-radius: 4px;
    background-color: #6EB5C0;
    border: none;
    color: #E2E8E4;
    text-align: center;
    font-size: 1.25rem;
    padding: 10px;
    width: 200px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
  }
  
  span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  button:hover span {
    padding-right: 25px;
    color: #FFCCBB;
  }
  
  button:hover span:after {
    opacity: 1;
    right: 0;
    color: #FFCCBB;
  }
  .textareaField {
    width: 70%;
  }